.main{
	width: 100%;
	min-height: 100%;
	background: url(../img/main-bg.png) center center no-repeat;
	-webkit-background-size: cover;
	background-size: cover;
	padding-bottom: 45px;

	&-content{
		margin-top: 52px;
		height: 100%;
		position: relative;

		&:after{
			content:'';
			width: 690px;
			height: 590px;
			position: absolute;
			bottom: -8%;
			left: 48%;
			transform: translateX(-48%);
			background: url(../img/man.png)center center no-repeat;

			@include tablets{
				left: 65%;
			}
		}
	}

	&-block{
		display: flex;
		align-items: center;
		margin-top: 60px;

		&__col{
			width: 61%;
			margin-right: 80px;

			&:last-child{
				flex:1;
				margin-right:0;

				@include tablets{
					margin-bottom: 0;
					text-align: center;
				}
			}

			@include tablets{
				width: 100%;
				margin-right:0;
				margin-bottom: 30px;
			}
		}

		@include monitor{
			position: relative;
			z-index: 2;
		}

		@include tablets{
			flex-direction: column;
		}
	}

}