.advantages__item {
  margin-bottom: 25px;
  background-color: rgba(0, 23, 38, 0.9);
  padding: 15px 20px;
  display: flex;
  align-items: center;
}

.advantages__item:last-child {
  margin-bottom: 0;
}

.advantages__icon {
  width: 35px;
}

.advantages__icon-pic {
  max-height: 100%;
  max-width: 100%;
}

.advantages__content {
  margin-left: 17px;
  margin-top: -5px;
}

.burger {
  display: none;
  height: 25px;
  width: 30px;
  position: fixed;
  top: 10px;
  right: 20px;
  z-index: 15;
  cursor: pointer;
}

.burger .line {
  background-color: #fff;
  display: inline-block;
  width: 30px;
  height: 4px;
  border: 1px solid black;
  border-radius: 2px;
  position: absolute;
  top: 0;
  left: 0;
}

.burger .line:nth-child(2) {
  top: 10px;
}

.burger .line:nth-child(3) {
  top: 20px;
}

.getCross .line {
  display: inline-block;
  transform-origin: 50% 50%;
  background-color: #fff;
}

.getCross .line:nth-child(1) {
  display: none;
}

.getCross .line:nth-child(2) {
  transform: rotate(45deg);
  top: 50%;
}

.getCross .line:nth-child(3) {
  transform: rotate(-45deg);
  top: 50%;
}

.content {
  color: #fff;
}

.content__title {
  font-weight: 700;
  font-size: 24px;
  text-transform: uppercase;
  letter-spacing: 1px;
}

.content__title--black {
  color: black;
}

.content__descr {
  font-size: 14px;
  font-weight: 400;
}

.content__descr--black {
  color: black;
}

.form {
  display: inline-block;
  max-width: 360px;
  padding: 20px 35px;
  background-color: rgba(238, 242, 245, 0.9);
  text-align: center;
  position: relative;
  z-index: 2;
}

.form__content .content__title {
  margin-bottom: 18px;
  display: inline-block;
  padding: 0 35px;
}

.form__wrapper {
  margin-top: 21px;
}

.form__label {
  margin-bottom: 15px;
  display: flex;
  width: 100%;
}

.form__label--mb {
  margin-bottom: 0;
}

.form__input {
  background-color: #eef2f5;
  min-height: 40px;
  width: 100%;
  border: 1px solid #c9c9c6;
  outline: none;
  font-size: 20px;
  color: grey;
  padding: 10px 20px;
}

.form__icon {
  display: inline-flex;
  align-items: center;
  justify-content: center;
  width: 40px;
  flex-shrink: 0;
  border: 1px solid #c9c9c6;
  border-left: none;
}

.form__icon-pic {
  max-width: 100%;
  max-height: 100%;
}

.form__button {
  width: 100%;
  margin-top: 22px;
}

.form__checkbox {
  display: flex;
  align-items: center;
  margin-top: 13px;
}

.form__checkbox-none {
  display: none;
}

.form__checkbox-none:checked + .form__checkbox-fake {
  border-color: red;
}

.form__checkbox-none:checked + .form__checkbox-fake:after {
  width: 7px;
  height: 7px;
}

.form__checkbox-fake {
  height: 16px;
  width: 16px;
  border: 1px solid #cacac6;
  position: relative;
  transition: border-color .4s;
}

.form__checkbox-fake:after {
  content: '';
  display: inline-block;
  width: 0px;
  height: 0px;
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  background-color: #dd2c14;
}

.form__checkbox-content {
  text-align: left;
  margin-left: 8px;
  display: inline-block;
}

.form__checkbox-content .content__descr {
  color: #999999;
}

.fullscreen {
  position: fixed;
  z-index: 10;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  width: 100%;
  height: 100vh;
  overflow: hidden;
  background: rgba(255, 255, 255, 0.9);
  opacity: 0;
  visibility: hidden;
}

.fullscreen__menu {
  height: 100%;
  width: 100%;
  display: flex;
  justify-content: center;
  align-items: center;
}

.fullscreen__menu .menu {
  display: block;
}

.fullscreen__menu .menu__list {
  flex-direction: column;
}

.fullscreen__menu .menu__item {
  margin-bottom: 30px;
}

.fullscreen__menu .menu__item:last-child {
  margin-bottom: 0;
}

.fullscreen__menu .menu__link {
  font-size: 35px;
}

.is-active {
  opacity: 1;
  visibility: visible;
}

.header__row {
  display: flex;
  align-items: center;
  justify-content: space-between;
}

.header__row--top {
  background-color: rgba(0, 23, 38, 0.2);
  padding: 20px 0;
  margin-bottom: 25px;
}

.header__row .container {
  display: flex;
  align-items: center;
  justify-content: space-between;
}

.header__telephone {
  margin-top: -6px;
}

.logo {
  display: flex;
  align-items: center;
}

.logo__link {
  display: inline-block;
}

.logo__img {
  max-height: 100%;
  max-width: 100%;
}

.logo__info {
  font-size: 17px;
  font-weight: 700;
  text-transform: uppercase;
  color: black;
  width: 50%;
  margin-left: 15px;
  line-height: 24px;
}

.main {
  width: 100%;
  min-height: 100%;
  background: url(../img/main-bg.png) center center no-repeat;
  -webkit-background-size: cover;
  background-size: cover;
  padding-bottom: 45px;
}

.main-content {
  margin-top: 52px;
  height: 100%;
  position: relative;
}

.main-content:after {
  content: '';
  width: 690px;
  height: 590px;
  position: absolute;
  bottom: -8%;
  left: 48%;
  transform: translateX(-48%);
  background: url(../img/man.png) center center no-repeat;
}

.main-block {
  display: flex;
  align-items: center;
  margin-top: 60px;
}

.main-block__col {
  width: 61%;
  margin-right: 80px;
}

.main-block__col:last-child {
  flex: 1;
  margin-right: 0;
}

.menu {
  width: 100%;
}

.menu__list {
  width: 100%;
  display: flex;
  align-items: center;
  justify-content: space-between;
}

.menu__link {
  color: black;
  font-size: 16px;
  font-weight: 600;
}

.menu__link:hover,
.menu__link:focus {
  color: grey;
}

.modal {
  position: fixed;
  z-index: 2;
  top: 0;
  left: 0;
  bottom: 0;
  right: 0;
  width: 100%;
  height: 100vh;
  background: rgba(128, 128, 128, 0.8);
  overflow: hidden;
  display: none;
}

.modal__form {
  display: flex;
  justify-content: center;
  align-items: center;
  width: 100%;
  height: 100%;
}

.modal__form .form__cross {
  display: inline-block;
  position: absolute;
  top: 10px;
  right: 20px;
  height: 25px;
  width: 25px;
  cursor: pointer;
}

.modal__form .form__cross:hover .line {
  background-color: black;
}

.modal__form .form__cross .line {
  display: inline-block;
  width: 100%;
  height: 2px;
  background-color: red;
  transform-origin: 50% 50%;
  position: absolute;
  top: 0;
  left: 0;
}

.modal__form .form__cross .line:nth-child(1) {
  transform: rotate(45deg);
  top: 50%;
}

.modal__form .form__cross .line:nth-child(2) {
  transform: rotate(-45deg);
  top: 50%;
}

.open {
  display: block;
}

.red-block {
  display: inline-block;
  background-color: #dd2c14;
  width: 620px;
  padding: 28px 40px;
  position: relative;
}

.red-block__title {
  color: #fff;
  font-size: 36px;
  font-weight: 700;
  text-transform: uppercase;
}

.red-block:after {
  content: '';
  display: inline-block;
  border: 50px solid transparent;
  border-bottom: 50px solid #dd2c14;
  position: absolute;
  top: 68%;
  right: 4%;
  transform: rotate(45deg);
}

.telephone {
  display: flex;
  justify-content: center;
  flex-direction: column;
  text-align: right;
}

.telephone__number {
  font-size: 20px;
  font-weight: 700;
  color: black;
  text-transform: uppercase;
  transition: .4s;
  margin-bottom: 3px;
  white-space: nowrap;
}

.telephone__number:hover {
  color: grey;
}

.telephone__call {
  color: #dd2c14;
  font-weight: 400;
  font-size: 14px;
  text-decoration: underline;
  transition: .4s;
}

.telephone__call:hover {
  color: black;
}

/*base styles*/

* {
  box-sizing: border-box;
  transition: .5s;
}

html,
body {
  padding: 0;
  margin: 0;
  height: 100%;
  width: 100%;
  font-family: 'Exo 2', sans-serif;
  font-size: 14px;
}

ul {
  margin: 0;
  padding: 0;
  list-style: none;
}

a {
  text-decoration: none;
  color: #fff;
}

.wrapper {
  position: relative;
  min-height: 100%;
  width: 100%;
  overflow: hidden;
  display: flex;
  flex-direction: column;
}

.section {
  height: 100vh;
  width: 100vw;
}

.container {
  width: 1140px;
  max-width: 100%;
  height: 100%;
  margin: 0 auto;
}

.button {
  outline: none;
  border: 1px solid transparent;
  color: #fff;
  font-size: 24px;
  font-weight: 700;
  text-transform: uppercase;
  background-color: #dd2c14;
  cursor: pointer;
  width: 100%;
  padding: 8px 0;
  transition: .4s;
  font-family: 'Exo 2', sans-serif;
  letter-spacing: 1px;
}

.button:hover {
  background-color: #c8594b;
}

.overflow {
  overflow: hidden;
}

/*base end*/

@media screen and (max-width: 1200px) {
  .main-block {
    position: relative;
    z-index: 2;
  }

  .container {
    padding: 0 3%;
  }
}

@media screen and (max-width: 768px) {
  .advantages__item {
    justify-content: center;
  }

  .burger {
    display: block;
  }

  .main-content:after {
    left: 65%;
  }

  .main-block__col:last-child {
    margin-bottom: 0;
    text-align: center;
  }

  .main-block__col {
    width: 100%;
    margin-right: 0;
    margin-bottom: 30px;
  }

  .main-block {
    flex-direction: column;
  }

  .menu {
    display: none;
  }

  .red-block {
    display: block;
    margin: 0 auto;
    max-width: 100%;
    text-align: center;
  }
}

@media screen and (max-width: 480px) {
  .logo__info {
    font-size: 12px;
  }

  .red-block__title {
    font-size: 25px;
  }

  .red-block:after {
    display: none;
  }

  .telephone__number {
    font-size: 16px;
  }
}