.red-block{
	display: inline-block;
	background-color: #dd2c14;
	width: 620px;
	padding: 28px 40px;
	position: relative;

	&__title{
		color: #fff;
		font-size: 36px;
		font-weight: 700;
		text-transform: uppercase;

		@include phones{
			font-size: 25px;
		}
	}

	&:after{
		content: '';
		display: inline-block;
	    border: 50px solid transparent;
	    border-bottom: 50px solid #dd2c14;
	    position: absolute;
	    top: 68%;
		right: 4%;
	    transform: rotate(45deg);

	    @include phones{
	    	display: none;
	    }
	}

	@include tablets{
		display: block;
		margin: 0 auto;
		max-width: 100%;
		text-align: center;
	}
}