.form{
	display: inline-block;
	max-width: 360px;
	padding: 20px 35px;
	background-color:  rgba(238, 242, 245, 0.9);
	text-align: center;
	position: relative;
	z-index: 2;

	&__content{
		.content{
			&__title{
				margin-bottom: 18px;
				display: inline-block;
				padding: 0 35px;
			}
		}
	}

	&__wrapper{
		margin-top: 21px;
	}

	&__label{
		margin-bottom: 15px;
		display: flex;
		width: 100%;

		&--mb{
			margin-bottom:0;
		}
	}
	
	&__input{
		background-color: #eef2f5;
		min-height: 40px;
		width: 100%;
		border:1px solid #c9c9c6;
		outline: none;
		font-size: 20px;
		color: grey;
		padding: 10px 20px;
	}

	&__icon{
		display: inline-flex;
		align-items: center;
		justify-content: center;
		width: 40px;
		flex-shrink: 0;
		border:1px solid #c9c9c6;
		border-left: none;

		&-pic{
			max-width: 100%;
			max-height: 100%;
		}
	}

	&__button{
		width: 100%;
		margin-top: 22px;
	}

	&__checkbox{
		display: flex;
		align-items: center;
		margin-top: 13px;

		&-none{
			display: none;

			&:checked + .form__checkbox-fake{
				border-color:red;
			}

			&:checked + .form__checkbox-fake:after{
				width: 7px;
				height: 7px;
			}
		}

		&-fake{
			height: 16px;
    		width: 16px;
			border:1px solid #cacac6;
			position: relative;
			transition: border-color .4s;

			&:after{
				content:'';
				display: inline-block;
				width: 0px;
				height: 0px;
				position: absolute;
				top: 50%;
				left: 50%;
				transform: translate(-50%, -50%);
				background-color: #dd2c14;
			}
		}

		&-content{
			text-align: left;
			margin-left: 8px;
			display: inline-block;

			.content{
				&__descr{
					color: #999999;
				}
			}
		}
	}
}