.fullscreen{
	position: fixed;
	z-index: 10;
	top: 0;
	left: 0;
	right: 0;
	bottom: 0;
	width: 100%;
	height: 100vh;
	overflow:hidden;
	background: rgba(#fff, 0.9);

	opacity: 0;
	visibility: hidden;

	&__menu{
		height: 100%;
		width: 100%;

		display: flex;
		justify-content: center;
		align-items: center;

		.menu{
			display: block;

			&__list{
				flex-direction: column;
			}

			&__item{
				margin-bottom: 30px;

				&:last-child{
					margin-bottom:0;
				}
			}

			&__link{
				font-size: 35px;
			}
		}
	}
}

.is-active{
	opacity: 1;
	visibility: visible;
}