.telephone{
	display: flex;
	justify-content: center;
	flex-direction: column;
	text-align: right;

	&__number{
		font-size: 20px;
		font-weight: 700;
		color: black;
		text-transform: uppercase;
		transition: .4s;
		margin-bottom: 3px;
		white-space: nowrap;

		&:hover{
			color:grey;
		}

		@include phones{
			font-size: 16px;
		}
	}

	&__call{
		color: #dd2c14;
		font-weight: 400;
		font-size: 14px;
		text-decoration: underline;
		transition: .4s;

		&:hover{
			color: black;
		}
	}
}