.logo{
	display: flex;
	align-items: center;

	&__link{
		display: inline-block;
	}

	&__img{
		max-height: 100%;
		max-width: 100%;
	}

	&__info{
		font-size: 17px;
		font-weight: 700;
		text-transform: uppercase;
		color: black;
		width: 50%;
		margin-left: 15px;
		line-height: 24px;

		@include phones{
			font-size: 12px;
		}
	}
}