.modal{
	position: fixed;
	z-index: 2;
	top: 0;
	left: 0;
	bottom: 0;
	right: 0;
	width: 100%;
	height: 100vh;
	background: rgba(grey, 0.8);
	overflow:hidden;
	display: none;

	&__form{
		display: flex;
		justify-content: center;
		align-items: center;

		width: 100%;
		height: 100%;

		.form{
			&__cross{
				display: inline-block;
				position: absolute;
				top: 10px;
				right: 20px;
				height: 25px;
				width: 25px;

				cursor: pointer;

				&:hover{
					.line{
						background-color:black;
					}
				}

				.line{
					display: inline-block;
					width: 100%;
					height: 2px;
					background-color: red;
					transform-origin: 50% 50%;

					position: absolute;
			        top: 0;
			        left: 0;

			        &:nth-child(1){
			            transform: rotate(45deg);
			            top: 50%;
			        }

			        &:nth-child(2){
			            transform: rotate(-45deg);
			            top: 50%;
			        }
				}
			}
		}
	}
}

.open{
	display: block;
}