.advantages{
	

	&__list{}

	&__item{
		margin-bottom: 25px;
		background-color:  rgba(0, 23, 38, 0.9);
		padding: 15px 20px;

		display: flex;
		align-items: center;

		&:last-child{
			margin-bottom:0;
		}

		@include tablets{
			justify-content: center;
		}
	}

	&__icon{
		width: 35px;

		&-pic{
			max-height: 100%;
			max-width: 100%;
		}
	}

	&__content{
		margin-left: 17px;
		margin-top: -5px;
	}
}