.menu{
	width: 100%;

	&__list{
		width: 100%;
		display: flex;
		align-items: center;
		justify-content: space-between;
	}

	&__item{}

	&__link{
		color: black;
		font-size: 16px;
		font-weight: 600;

		&:hover,&:focus{
			color:grey;
		}
	}

	@include tablets{
		display: none;
	}
}