.burger{
    display: none;
    height: 25px;
    width: 30px;

    position: fixed;
    top: 10px;
    right: 20px;

    z-index: 15;

    cursor: pointer;
    

    .line{
        background-color: #fff;
        display: inline-block;
        width: 30px;
        height: 4px;
        border: 1px solid black;
        border-radius: 2px;

        position: absolute;
        top: 0;
        left: 0;

        &:nth-child(1){

        }

        &:nth-child(2){
            top: 10px;
        }

        &:nth-child(3){
            top: 20px;
        }
    }

    @include tablets{
        display: block;
    }
}

.getCross{

    .line{
        display: inline-block;
        transform-origin: 50% 50%;

        background-color: #fff;
        &:nth-child(1){
            display: none;
        }

        &:nth-child(2){
            transform: rotate(45deg);
            top: 50%;
        }

        &:nth-child(3){
            transform: rotate(-45deg);
            top: 50%;
        }
    }
}