/*base styles*/


    *{
        box-sizing: border-box;
        transition: .5s;
    }

    html, body{
        padding: 0;
        margin: 0;
        height: 100%;
        width: 100%;
        font-family: 'Exo 2', sans-serif;
        font-size: 14px;

    }

    ul{
        margin: 0;
        padding: 0;
        list-style: none;
    }

    a{
        text-decoration: none;
        color: #fff;
    }

    .wrapper{
        position: relative;
        min-height: 100%;
        width: 100%;
        overflow: hidden;

        display: flex;
        flex-direction: column;
    }

    .section{
        height: 100vh;
        width: 100vw;
    }

    .container{
        width: 1140px;
        max-width: 100%;
        height: 100%;
        margin: 0 auto;

        @include monitor{
            padding: 0 3%;
        }
    }

    .button{
        outline: none;
        border:1px solid transparent;
        color: #fff;
        font-size: 24px;
        font-weight: 700;
        text-transform: uppercase;
        background-color: #dd2c14;
        cursor: pointer;
        width: 100%;
        padding: 8px 0;
        transition: .4s;
        font-family: 'Exo 2', sans-serif;
        letter-spacing: 1px;

        &:hover{
            background-color:#c8594b;
        }
    }


    .overflow{
        overflow:hidden;
    }

/*base end*/
